* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: white;
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg {
  background: url('./assets/portal.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 100vh;
  /* ff 3.6+ */
  /* background:-moz-radial-gradient(circle at 3% 25%, rgba(5, 32, 63, 1) 0%, rgba(0, 0, 0, 1) 100%); */
  /* safari 5.1+,chrome 10+ */
  /* background:-webkit-radial-gradient(circle at 3% 25%, rgba(5, 32, 63, 1) 0%, rgba(0, 0, 0, 1) 100%); */
  /* opera 11.10+ */
  /* background:-o-radial-gradient(circle at 3% 25%, rgba(5, 32, 63, 1) 0%, rgba(0, 0, 0, 1) 100%); */
  /* ie 10+ */
  /* background:-ms-radial-gradient(circle at 3% 25%, rgba(5, 32, 63, 1) 0%, rgba(0, 0, 0, 1) 100%); */
  /* global 92%+ browsers support */
  /* background:radial-gradient(circle at 3% 25%, rgba(5, 32, 63, 1) 0%, rgba(0, 0, 0, 1) 100%); */
}

.gradient__bg2 {
  /* ff 3.6+ */
  background: -moz-linear-gradient(180deg, rgba(7, 3, 110, 1) 0%, rgba(50, 174, 240, 1) 50%);
  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(180deg, rgba(7, 3, 110, 1) 0%, rgba(50, 174, 240, 1) 50%);
  /* opera 11.10+ */
  background: -o-linear-gradient(180deg, rgba(7, 3, 110, 1) 0%, rgba(50, 174, 240, 1) 50%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#32AEF0', endColorstr='#07036E', GradientType=1);
  /* ie 10+ */
  background: -ms-linear-gradient(180deg, rgba(7, 3, 110, 1) 0%, rgba(50, 174, 240, 1) 50%);
  /* global 94%+ browsers support */
  background: linear-gradient(180deg, rgba(7, 3, 110, 1) 0%, rgba(50, 174, 240, 1) 50%);
}

.gradient__bg3 {
  /* ff 3.6+ */
  background: -moz-linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(40, 40, 40, 1) 100%);
  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(40, 40, 40, 1) 100%);
  /* opera 11.10+ */
  background: -o-linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(40, 40, 40, 1) 100%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF', endColorstr='#282828', GradientType=1);
  /* ie 10+ */
  background: -ms-linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(40, 40, 40, 1) 100%);
  /* global 94%+ browsers support */
  background: linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(40, 40, 40, 1) 100%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .gradient__bg {
    height: 80vh;
  }
}

@media screen and (max-width: 550px) {
  .gradient__bg {
    height: 70vh;
  }
}