.mw__navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    flex: 1;
    width: 100vw;
    overflow: hidden;
    background-color: rgba(9, 14, 56, 0.548);
}

.mw__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.mw__navbar-links-logo {
    margin-right: 2rem;
    padding: 2rem;
}

.mw__navbar-links_logo img {
    width: 64px;
    height: 64px;
    margin-left: 4rem;
}

.mw__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.mw__navbar-links_container p,
.mw__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.mw__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
    margin-right: 2rem;
}

.mw__navbar-menu svg {
    cursor: pointer;
}

.mw__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: fixed;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.mw__navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    .mw__navbar-links_container {
        display: none;
    }

    .mw__navbar-menu {
        display: flex;
    }

    .mw__navbar-links_logo img {
        margin-left: 1.5rem;
    }
}

@media screen and (max-width: 700px) {
    .mw__navbar-links_logo img {
        margin-left: 2rem;
    }
}

@media screen and (max-width: 550px) {
    .mw__navbar {
        padding: 2rem;
    }

    .mw__navbar-menu_container {
        top: 80px;
    }

    .mw__navbar-links_logo img {
        margin-left: 0rem;
    }
}