.mw__skills {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
}

.mw__skills-title {
    justify-content: center;
    align-items: center;
    padding: 6rem 5rem 0;
}

.mw__skills-divider {
    height: 0.4rem;
    max-width: 3.25rem;
    margin: 1rem auto;
    background-color: rgb(107, 103, 103);
    opacity: 1;
}

.mw__skills-title h1 {
    color: rgb(12, 16, 34);
    font-weight: 700;
    font-family: var(--font-family);
    font-size: 48px;
}

.mw__skills-container {
    display: flex;
    /* flex-wrap: wrap; */
    align-items: center;
    justify-content: center;
    margin: 6rem 6rem 3rem;
    /* background-color: rgb(153, 207, 247); */
    background: url("../../assets/ripples.jpg");
    background-size: cover;
    background-position: 0;
    border-radius: 50px;
}

.mw__skills-container>div {
    flex: 1;
    max-width: 100%;
    min-width: 30px;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mw__skills:hover .mw__skills-icon-1 {
    position: relative;
    animation: example var(--animation-time) var(--animation-count);
    animation-timing-function: var(--animation-style);
    animation-delay: 0s;
}

