    animation-delay: 2s;
}

@keyframes example {
    0% {
        top: 0px;
    }

    50% {
        top: 100px;
    }

    100% {
        top: 0px;
    }
}

@media screen and (max-width: 1000px) {
    .mw__skills-container>div {
        margin: 1rem .5rem;
    }

    .mw__skills-container {
        margin: 6rem 4rem 3rem;
    }
}

@media screen and (max-width: 700px) {
    .mw__skills-container>div {
        margin: .5rem .5rem;
        min-width: 20px;
        max-height: 55px;
    }

    .mw__skills-container {
        margin: 6rem 2rem 3rem 1rem;
    }
}

@media screen and (max-width: 450px) {
    .mw__skills-container>div {
        margin: .5rem .5rem;
        min-width: 15px;
        max-height: 25px;
    }

    .mw__skills-container {
        margin: 3rem 2rem 3rem 1rem;
    }
}